}

.info {
  height: 60px;
  align-items: center;
}

.display {
  width: 54px;
  display: flex;
  justify-content: space-between;
}

.display img {
  width: 20px;
  cursor: pointer;
  -webkit-filter: invert(50%);
          filter: invert(50%);
}

.info {
  display: flex;
  justify-content: space-between;
}

a .nameEnList, a .nameRuList{
  text-decoration: none;
}

.display img.activeDisplayIcon {
  -webkit-filter: invert(0);
