}
.person {
  width: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: white;
  height: 242px;
  padding: 10px;
  border-radius: 3px;
  box-shadow: 0 2px 6px 0 #a1a1a1;
  margin-bottom: 20px;
  transition: .2s;
}

.person:hover {
  transform: scale(1.05);
  transition: .5s;
}

.nameEnTile {
  color: black;
  margin-bottom: 5px;
}

.nameRuTile {
  font-size: 12px;
