  margin-bottom: 10px;
}

.personInfo {
  display: flex;
  justify-content: space-between;
  width: 77%;
  border-top: 1px solid #00000033;
  padding-top: 20px;
}

.personInfo img {
  width: 14px;
  float: left;
  margin-right: 5px;
}

.personInfo div {
  color: black;
  font-size: 12px;
}
.personList {
  display: flex;
  align-items: center;
  background-color: white;
  padding: 15px;
  border-bottom: 1px solid #a1a1a1;
  font-size: 13px;
  transition: .3s;
}

.personList:hover {
  background-color: rgb(245, 245, 245);
  transition: .5s;
}

.personList div {
  color: black;
}

.personList img {
  width: 50px;
  float: left;
  border-radius: 35px;
  margin-right: 13%;
}

.nameEnList {
  color: black;
  font-size: 17px;
