}

.personPageContainer .photoNameContainer {
  display: flex;
  text-align: center;
  padding: 30px 120px 30px 0;
}

.personPageContainer .photoName {
  margin-left: 10px;
}

.personPageContainer .photoName img {
  width: 150px;
  border-radius: 80px;
  margin-bottom: 17px;
}

.personPageContainer .photoName img+p {
  font-size: 12px;
  margin-bottom: 8px;
  color: #a7a7a7;
} 

.personPageContainer .nameEn {
  margin-bottom: 8px;
  font-size: 16px;
}

.personPageContainer .nameRu {
  font-size: 12px;
  margin-bottom: 8px;
  color: #a7a7a7;
}

.photoName>div {
  display: flex;
  font-size: 11px;
