}

.imgNameContainer {
  display: flex;
  align-items: center;
  padding: 10px 10px 10px 20px;
  width: 100%;
  cursor: pointer;
}

.imgNameContainer img {
  width: 50px;
  border-radius: 30px;
  float: left;
  margin-right: 15px;
}

.imgNameContainer .name {
  font-size: 14px;
  text-decoration: underline;
}

.addressBookRole, .vacationRole, .admin {
  display: flex;
  align-items: center;
  padding-left: 20px;
}

.addressBookRole {
  width: 26%;
}

.vacationRole {
  width: 30%;
}

.active, .disabled {
  padding: 8px 17px;
  border-radius: 1px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 68px;
  cursor: pointer;
  font-size: 13px;
  -webkit-touch-callout: none; 
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  transition: .5s;
}

.active {
  background-color: rgb(73, 73, 255);
  color: white;
  border: 1px solid rgb(73, 73, 255);
}

.disabled {
  color: #49494994;
  border: 1px solid #dadaff94;
}


